import React from 'react';
import classnames from 'classnames/bind';

import Icon from '@linkexchange/components/src/Icon';

import * as style from './link.scss';

const cx = classnames.bind(style);

const Link = ({ link, className }) => (
  <div className={cx('self', className)}>
    <div className={style.title}>{link.title}</div>
    <div className={style.summary}>{link.summary}</div>
    <Icon className={style.icon} name="external-link" />
  </div>
);

export default Link;



